@import './global.sass'
.nohaylugar
    display: flex
    justify-content: center
    position: relative
    overflow: hidden
    padding: 3rem
    &__figures
        width: 100%
        height: 100%
        position: absolute
        top: 0
        left: 0
        z-index: 1
        .resorte
            position: absolute
            bottom: 3rem
            left: 0
            width: 80px
            z-index: 1
        .rayas
            position: absolute
            top: 3rem
            right: 3rem
            width: 80px
            z-index: 1

    &__image
        width: 100%
        display: flex
        justify-content: center
        img
            width: 100%
            max-width: 300px
            height: 100%
            object-fit: cover
            object-position: center
